import React from "react";
import ReactDOM from "react-dom/client";
import "./index.css";
import reportWebVitals from "./reportWebVitals";
import { BrowserRouter, HashRouter, Routes, Route } from "react-router-dom";
import Homepage from "./pages/Homepage";
import AvocademyPage from "./pages/AvocademyPage";
import SodaPage from "./pages/SodaPage";
import CraigslistPage from "./pages/CraigslistPage";
import PhysicsPage from "./pages/PhysicsPage";
import AboutPage from "./pages/AboutPage";

const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);
root.render(
  <HashRouter>
    <Routes>
      <Route path="/">
        <Route index element={<Homepage />} />
        <Route path="avocademy" element={<AvocademyPage />} />
        <Route path="freestyle" element={<SodaPage />} />
        <Route path="craigslist" element={<CraigslistPage />} />
        <Route path="physims" element={<PhysicsPage />} />
        <Route path="about" element={<AboutPage />} />
      </Route>
    </Routes>
  </HashRouter>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
